  font-size: 60px;
  overflow: hidden;
}
p {
  color: white;
  text-align: center;
  font-family: "Raleway", sans-serif;
  width: 50%;
  padding: 20px;
  opacity: 0.4;
}
.dynamic {
  font-family: red-dead;
  letter-spacing: 10px;
  color: rgb(183, 194, 218);
  opacity: 0;
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 3000ms;
          animation-duration: 3000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes example {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  10% {
    opacity: 1;
    transform: translateY(0px);
  }
  80% {
    opacity: 1;
  }
  99% {
    opacity: 0;
  }
}
@keyframes example {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  10% {
    opacity: 1;
    transform: translateY(0px);
  }
  80% {
    opacity: 1;
  }
  99% {
    opacity: 0;
  }
}

.line {
  border: 1px solid;
  height: 30px;
  background-color: whitesmoke;
  opacity: 0.7;
  align-items: center;
}
.welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 120px;
  font-size: 10px;
  text-transform: uppercase;
  color: whitesmoke;
  opacity: 0.4;
  z-index: 0;
}
.welcome h3 {
  overflow: hidden;
}
