@media screen and (max-width: 1170px) {
  .navbar .nav-items {
    display: none;
  }
  .menu li:not(:last-child) {
    display: block;
  }
  .menu .nav-items {
    display: block;
    text-align: center;
  }
  .navbar .icon {
    display: block;
